*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  background-image: url('assets/winter.jpg') ;
  background-position: bottom;
  background-size: cover;
  transition: 0.4 ease-out;
}

.app.warm{
  background-image: url('assets/sunny.jpg') ;
}

main{
  min-height: 100vh;
  padding: 25px 18% 0 18%;
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 16px;
  /* margin-top: -25px; */

  box-shadow: 0 5px rgba(0, 0, 0, 0.2);

  font-size: 20px;
  transition: 0.3s ease;
}

.search-box .search-bar:focus{
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location{
  color: white;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: white;
  font-size: 25px;
  font-weight: 300;
  text-align: center;
  font-style: italic;
  text-shadow: 3px rgba(50, 50, 70, 0.5);
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 15px;

  color: white;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);

}

.weather-box .weather{
  color: white;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px rgba(50, 50, 70, 0.5);
}

.index-box .index{
  color: #5DCFEF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(53, 53, 75, 0.5);
}

.index-box .input-text {
  color: white;
  font-size: 25px;
  font-weight: 300;
  text-align: center;
  font-style: italic;
  text-shadow: 3px rgba(50, 50, 70, 0.5);
}
